import { Component, OnInit } from '@angular/core';
import { Record, AuthorInterface, Order } from '../common.models';
import {
  Http, Response, RequestOptions,
  Headers, URLSearchParams
} from '@angular/http';

import * as _ from 'lodash';

import { PriceLimits } from '../filter-records/filter-records.component';
import { Stavka } from '../stavka';
import { Kategorija } from '../kategorija';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainComponent implements OnInit {
  public records: Record[];
  public kategorijas: Kategorija[];
  public stavkas: Stavka[];
  public newRecord: Record;
  public newStavka: Stavka;

  public orderTypes = Order;
  public priceFilter: PriceLimits;
  public authors: AuthorInterface[];

  constructor(private http: Http) {
    this.priceFilter = {
      lowest: 0,
      highest: 0
    }
    this.kategorijas = [];
    this.stavkas = [];
    this.records = [];
    this.loadData();

  }

  ngOnInit() {
  }

  private loadData(order?: Order) {
    let params: URLSearchParams = new URLSearchParams();
    params.set('lowestPrice', this.priceFilter.lowest.toString());
    params.set('highestPrice', this.priceFilter.highest.toString());
    if (order !== undefined) {
      params.set('ord', order.toString());
    }
    let options = new RequestOptions({ search: params });
    this.http.get('/api/records', options).subscribe(
      //koristimo arrow funkciju da bismo imali leksicki
      //opseg this objekata
      (res: Response) => {
        this.records = res.json();
      }
    );

    this.http.get('/api/stavkas', options).subscribe(
      (res: Response) => {
        this.stavkas = res.json();
      }
    );

    this.http.get('/api/kategorijas', options).subscribe(
      (res: Response) => {
        this.stavkas = res.json();
      }
    );
  }

  save(newRecord: Record) {
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    this.http.post('/api/records', JSON.stringify(newRecord), options).subscribe(
      (res: Response) => {
        this.loadData();
      }
    );
  }

  saveStavka(newStavka: Stavka) {
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    this.http.post('/api/stavkas', JSON.stringify(newStavka), options).subscribe(
      (res: Response) => {
        this.loadData();
      }
    );
  }

  updateStavka(id: number, newStavka: Stavka) {
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    this.http.put('/api/stavkas/' + id, JSON.stringify(newStavka), options).subscribe(
      (res: Response) => {
        this.loadData();
      }
    );
  }

  delete(id: number) {
    this.http.delete('/api/records/' + id).subscribe(
      (res: Response) => {
        this.loadData();
      }
    );
  }

  deleteStavka(id: number) {
    this.http.delete('/api/delete/' + id).subscribe(
      (res: Response) => {
        this.loadData();
      }
    );
  }

  filterRecords(priceLimits: PriceLimits) {
    this.priceFilter = priceLimits;
    this.loadData();
  }

  resetFilter() {
    this.priceFilter = { lowest: 0, highest: 0 };
  }

}




