import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';
import { Stavka } from '../stavka';
import { Kategorija } from '../kategorija';
import { Http, Response } from '@angular/http';

@Component({
  selector: 'app-add-stavka',
  templateUrl: './add-stavka.component.html',
  styleUrls: ['./add-stavka.component.css']
})
export class AddStavkaComponent implements OnInit {

  public newStavka: Stavka;
  public stavkas: Stavka[];
  public kategorijas: Kategorija[];
  public JSON: Object;

  @Output()
  newStavkaEvent: EventEmitter<Stavka> = new EventEmitter();

  constructor(private http: Http) {
    this.http.get('/api/kategorijas').subscribe(
      // koristimo arrow funkciju da bismo imali leksicki
      // opseg this objekata
      (res: Response) => {
        this.kategorijas = res.json();
      }
    );
    this.newStavka = {
      naziv: '',
      cena: 0
    };
    this.JSON = JSON;
  }

  ngOnInit() {
  }


  loadData() {
  }

  addStavka() {
    this.newStavkaEvent.next(this.newStavka);
  }

}



