import { Component, OnInit,Output, EventEmitter } from '@angular/core';
import { Page } from '../page';

@Component({
  selector: 'app-filter-stavke',
  templateUrl: './filter-stavke.component.html',
  styleUrls: ['./filter-stavke.component.css']
})
export class FilterStavkeComponent implements OnInit {

  @Output()
  public setSizeLimit: EventEmitter<Page> = new EventEmitter();
  public page: Page;
  public JSON: Object = JSON;

  constructor() { }

  ngOnInit() {
  }

  stavkeFilter(){
    this.setSizeLimit.next(this.page);
  }

  resetStavkeFilter(){
    this.setSizeLimit.next({
      size: 0,
      number: 0,
      first: true,
      last: false
      }
    )
  }

}



