import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Stavka } from '../stavka';
import { Http, RequestOptions, Response } from '@angular/http';

@Component({
  selector: 'app-list-stavke',
  templateUrl: './list-stavke.component.html',
  styleUrls: ['./list-stavke.component.css']
})
export class ListStavkeComponent implements OnInit {

  @Input() stavkas: Stavka[];

  @Output() stavkaUpdateEvent: EventEmitter<number> = new EventEmitter();

  @Output() stavkaDeleteEvent: EventEmitter<number> = new EventEmitter();

  constructor(private http:Http) {
    this.stavkas = [];
    this.loadData();
  }

  loadData(){
    let params: URLSearchParams = new URLSearchParams();
    let options = new RequestOptions({ search: params });
    this.http.get('/api/stavkas',options).subscribe(
        // koristimo arrow funkciju da bismo imali leksicki
        // opseg this objekata
        (res: Response) => {
          this.stavkas=res.json();
        }
    );
  }

  ngOnInit() {
  }

  updateStavka(id:number){
    this.stavkaUpdateEvent.next(id);
  }

  deleteStavka(id:number){
    this.stavkaDeleteEvent.next(id);
  }

}



